import { Component } from '@angular/core';
import {MatDialog} from '@angular/material';
import { DialogOverviewExampleDialog } from './modalCustomerDetail/modalCustomerDetail.component';

export interface DialogData {
    animal: string;
    name: string;
}

@Component({
    templateUrl: './consignmentdetails.component.html',
    styleUrls: ['./consignmentdetails.component.scss']
})

export class ConsignmentdetailsComponent{
    statusFlagsFromSidebar: any = {}
    animal: string;
    name: string;

    constructor(public dialog: MatDialog){}
    recieveDashboardClickEvent($event){
        this.statusFlagsFromSidebar = $event
    }

    openDialog(): void {
        const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
          width: '900px',
          data: {name: this.name, animal: this.animal}
        });
    
        dialogRef.afterClosed().subscribe(result => {
          this.animal = result;
        });
      }
} 