import { RouterModule, Routes } from '@angular/router';
import { DashboardComponent }   from './dashboard/dashboard.component';
import { ConsignmentdetailsComponent }   from './consignmentdetails/consignmentdetails.component';

const routes: Routes = [
    {path: '', redirectTo: 'dashboard', pathMatch: 'full' },
    {path: 'dashboard', component: DashboardComponent},
    {path: 'consignmentdetails', component: ConsignmentdetailsComponent},
]

export const AppRoutes = RouterModule.forRoot(routes, { initialNavigation: 'disabled' });